html, body {
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  max-width: 800px;
  margin: 0 auto;
  display: grid;
}

.person {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo {
  position: relative;
}

.photo img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 0 auto;
  display: block;
}

.rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  line-height: 32px;
  position: absolute;
  top: 84px;
  right: 5px;
}

.details .name {
  margin-top: 10px;
  font-weight: bold;
}

.details .extra {
  margin-top: 5px;
  font-size: .8em;
}

@media screen and (width <= 600px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
  }

  .details .name {
    font-size: 1.2em;
  }

  .details .extra {
    font-size: 1em;
  }
}
